// ‰∏Ä‰∏™ÁªÑ‰ª∂ÂØπÂ∫î‰∏Ä‰∏™watcher
let id = 0;
import { popTarget, pushTarget } from './dep';
import { queueWatcher } from './scheduler';

class Watcher {
    constructor(vm, exprOrFn, cb, options) {
        this.vm = vm
        this.exprOrFn = exprOrFn
        this.cb = cb
        this.options = options
        this.id = id++ // ÁªôwatcherÊ∑ªÂä†Ê†áËØÜ

        // ÈªòËÆ§Â∫îËØ•ÊâßË°åexprOrFn
        // exprOrFn ÂÅö‰∫ÜÊ∏≤ÊüìÂíåÊõ¥Êñ∞
        // ÊñπÊ≥ïË¢´Ë∞ÉÁî®ÁöÑÊó∂ÂÄôÔºå‰ºöÂèñÂÄº
        this.getter = exprOrFn
        this.deps = []
        this.depsId = new Set()
        // ÈªòËÆ§ÂàùÂßãÂåñÊâßË°åget
        this.get()
    }
    get() {
        pushTarget(this) // DepÁöÑtargetÂ∞±ÊòØ‰∏Ä‰∏™watcher

        /* ÂàõÂª∫ÂÖ≥ËÅî alert 
            * ÊØè‰∏™Â±ûÊÄßÈÉΩÂèØ‰ª•Êî∂ÈõÜËá™Â∑±ÁöÑwatcher
           * Â∏åÊúõ‰∏Ä‰∏™Â±ûÊÄßÂèØ‰ª•ÂØπÂ∫îÂ§ö‰∏™watcher
           * ‰∏Ä‰∏™watcherÂèØ‰ª•ÂØπÂ∫îÂ§ö‰∏™Â±ûÊÄß
        */
        // Á®çÂêéÁî®Êà∑Êõ¥Êñ∞ÁöÑÊó∂ÂÄôÂèØ‰ª•ÈáçÊñ∞Ë∞ÉÁî®getÊñπÊ≥ï
        this.getter()
        popTarget() // ËøôÈáåÂéªÈô§Dep.target,ÊòØÈò≤Ê≠¢Áî®Êà∑Âú®js‰∏≠ÂèñÂÄº‰∫ßÁîü‰æùËµñÊî∂ÈõÜ
    }
    update() {
        // ÊØèÊ¨°Êõ¥Êñ∞Êó∂ÔºåÊääwatcherÁºìÂ≠ò‰∏ãÊù• 
        // Â¶ÇÊûúÂ§öÊ¨°Ë∑üÊñ∞ÁöÑÊòØ‰∏Ä‰∏™watcherÔºåÂêàÂπ∂Êàê‰∏Ä‰∏™
        // vue‰∏≠ÁöÑÊõ¥Êñ∞ÊòØÂºÇÊ≠•ÁöÑ
        // this.get()
        queueWatcher(this)
    }

    run() { // ÂêéÁª≠Ë¶ÅÊúâÂÖ∂‰ªñÁöÑÂäüËÉΩ
        console.log('run')
        this.get()
    }
    addDep(dep) {
        let id = dep.id
        if (!this.depsId.has(id)) {
            this.depsId.add(id)
            this.deps.push(dep)
            dep.addSub(this)
        }
    }

}

// Âú®vue‰∏≠ÔºåÈ°µÈù¢Ê∏≤ÊüìÁöÑÊó∂ÂÄô‰ΩøÁî®ÁöÑÂ±ûÊÄßÔºåÈúÄË¶ÅËøõË°å‰æùËµñÊî∂ÈõÜ
// Êî∂ÈõÜÂØπË±°Ê∏≤ÊüìÁöÑwatcher
// Ê∏≤ÊüìÈ°µÈù¢ÂâçÔºå‰ºöÂ∞ÜÂΩìÂâçÁöÑwatcherÊîæÂà∞Dep‰∏ä
// Êàë‰ª¨Â∞ÜÊõ¥Êñ∞ÁöÑÂäüËÉΩÂ∞ÅË£ÖÊàê‰∏Ä‰∏™watcher
// ÂèñÂÄºÁöÑÊó∂ÂÄôÔºåÁªôÊØè‰∏™Â±ûÊÄßÈÉΩÂä†‰∫Ü‰∏Ä‰∏™depÂ±ûÊÄßÔºåÁî®‰∫éÂ≠òÂÇ®Ëøô‰∏™Ê∏≤Êüìwatcher => Âêå‰∏Ä‰∏™watcherÂØπÂ∫îÂ§ö‰∏™dep
// ÊØè‰∏™Â±ûÊÄßÔºåÂèØËÉΩÂØπÂ∫îÂ§ö‰∏™ËßÜÂõæÔºåÂ§ö‰∏™ËßÜÂõæÊúâÂ§ö‰∏™watcher => ‰∏Ä‰∏™Â±ûÊÄßË¶ÅÂØπÂ∫îÂ§ö‰∏™watcher
// ÂèåÂêëÂ≠òÂÇ® 
export default Watcher